<script>
  import {Select, Option, Button, Input, DatePicker, Radio, Message, MessageBox} from 'element-ui'
  import Editor from '@tinymce/tinymce-vue'
  import MySelect from 'src/components/UIComponents/Select'
  import MyMoney from 'src/components/UIComponents/Money'
  import { mapState } from 'vuex'

  export default {
    components: {
      ElSelect: Select,
      ElOption: Option,
      ElButton: Button,
      ElInput: Input,
      ElDatePicker: DatePicker,
      ElRadio: Radio,
      editor: Editor,
      MySelect,
      MyMoney
    },
    created() {
      let self = this;
      this.$validator.extend('earlier', {
        getMessage(field, val) {
          return 'Thời gian kết thúc phải lớn hơn thời gian bắt đầu'
        },
        validate(value, field) {
          let startParts = self.form.start_date
          let momentStart = new Date(startParts).getTime();
          let momentEnd = new Date(value).getTime();
          return momentEnd > momentStart;
        }
      })
    },
    data () {
      return {
        status: {
          options: [
            {
              title: "Hiển thị",
              value: "active"
            }, {
              title: "Ẩn",
              value: "inactive"
            }, {
              title: "Hết hạn",
              value: "expried"
            }
          ]
        },
        types: {
          options: [
            {
              title: "Giảm theo đơn giá",
              value: "value"
            }, {
              title: "Giảm theo phần trăm",
              value: "percent"
            }, {
              title: "Miễn phí giao hàng",
              value: "freeship"
            }
          ]
        }
      }
    },

    computed: {
      form: {
        get () {
          return this.$store.state.couponDetail;
        },
        set (value) {}
      }
    },

    mounted() {
      let id = this.$route.params.id;
      this.$store.dispatch('fetchCouponDetail', id);

      this.$store.dispatch('setPageTitle', 'Cập nhật mã giảm giá');
      this.$store.dispatch('setCurrentActions', [{
        label: 'Xóa',
        type: 'warning',
        icon: '',
        callback: this.remove
      }, {
        label: 'Cập nhật',
        type: 'primary',
        icon: '',
        callback: this.save
      }])
    },

    methods: {
      cancel() {
        this.$router.push({name: 'AllCoupons'});
      },
      remove(){
        let self = this;
        MessageBox.confirm('Bạn có chắc chắn xóa không?', 'Warning', {
          confirmButtonText: 'Đồng ý',
          cancelButtonText: 'Hủy bỏ',
          type: 'warning',
          center: true
        }).then(() => {
          self.$store.dispatch('removeCoupon', self.form.id).then(res => {
            Message({
              type: 'success',
              message: 'Đã xóa mã giảm giá'
            });
            self.$router.push({name: 'AllCoupons'});
          });
        }).catch(() => {
          Message({
            type: 'info',
            message: 'Hủy bỏ'
          });
        })
      },
      save() {
        let self = this;
        this.$validator.validateAll().then(function (result) {
          if (result) {
            self.$store.dispatch('updateCoupon', self.form).then(res => {
              Message({
                message: 'Cập nhật thành công',
                type: 'success'
              });
            }, res => {
              Message({
                message: res.message,
                type: 'danger'
              });
            });
          }
        })
      }
    },
    destroyed(){
      this.$store.dispatch('setCurrentActions', []);
    }
  }
</script>
